        </el-table-column>
        <el-table-column prop="status" label="状态" width="100"></el-table-column>
        <el-table-column prop="totalTokens" label="总令牌数" width="100"></el-table-column>
        <el-table-column prop="request" label="请求内容">
          <template slot-scope="scope">
            <pre style="white-space:pre-wrap;word-break:break-all;">{{ scope.row.request }}</pre>

          </template>
        </el-table-column>
        <el-table-column prop="response" label="响应内容">

          <template slot-scope="scope">
            <pre style="white-space:pre-wrap;word-break:break-all;">{{ scope.row.response }}</pre>
          </template>
        </el-table-column>
      </el-table>
      <div slot="footer">
        <el-button @click="showTaskLogDialog = false">关闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import {clientMixin} from "@/mixins/clientMixin";
import ClientConfig from "@/components/ClientConfig.vue";

const baseURL = "http://localhost:8091/api/v1/";

// Configure axios
axios.defaults.baseURL = baseURL;

export default {
  name: "AdminDashboard",
  mixins: [clientMixin],
  components: {
    ClientConfig,
  },
  data() {
    return {
      activeMenu: "clients",

      // Data arrays
      clients: [],
      prompts: [],
      knowledgeBase: [],
      tools: [],
      advisors: [],
      models: [],
      tasks: [],
      modelCollapseActiveNames: [],

      // Dialog visibility
      showClientConfig: false,
      showPromptDialog: false,
      showKnowledgeDialog: false,
      showGitDialog: false,
      showToolDialog: false,
      showAdvisorDialog: false,
      showModelDialog: false,
      showTaskDialog: false,
      showTaskLogDialog: false,

      // Form data
      clientForm: {
        id: null,
        clientName: "",
        description: "",
      },
      promptForm: {
        id: null,
        promptName: "",
        description: "",
        promptContent: "",
      },
      knowledgeForm: {
        ragName: "",
        knowledgeTag: "",
      },
      gitForm: {
        repoUrl: "",
        userName: "",
        token: "",
      },
      toolForm: {
        id: null,
        mcpName: "",
        basic: 0,
        transportType: "sse",
        transportConfig: `请输入JSON格式的配置参数，例如：
{
  "key1": "value1",
  "key2": "value2"
}`,
        requestTimeout: 5,
        status: 1,
      },
      advisorForm: {
        id: null,
        advisorName: "",
        basic: false,
        extParam: "{}",
        status: 1,
      },
      modelForm: {
        id: null,
        modelName: "",
        modelType: "",
        modelVersion: "",
        baseUrl: "",
        apiKey: "",
        completionsPath: "/v1/chat/completions",
        timeout: 30,
        status: 1,
      },
      taskForm: {
        id: null,
        agentId: "",
        taskName: "",
        description: "",
        cronExpression: "",
        taskParam: "",
        status: null,
      },
      taskLogs: [],

      fileList: [],
      editingClient: null,
    };
  },

  mounted() {
    this.loadData();
  },

  computed: {
    groupedModels() {
      // 按 modelName 分组
      const groups = {};
      this.models.forEach((model) => {
        if (!groups[model.modelName]) {
          groups[model.modelName] = [];
        }
        groups[model.modelName].push(model);
      });
      return groups;
    },
  },

  methods: {
    handleMenuSelect(key) {
      this.activeMenu = key;
      this.loadData();
    },

    getPageTitle() {
      const titles = {
        clients: "客户端管理",
        prompts: "系统提示管理",
        knowledge: "知识库管理",
        tools: "工具管理",
        advisors: "顾问管理",
        models: "模型管理",
        tasks: "智能体任务管理",
      };
      return titles[this.activeMenu] || "管理面板";
    },

    async loadData() {
      try {
        switch (this.activeMenu) {
          case "clients":
            await this.loadClients();
            break;
          case "prompts":
            await this.loadPrompts();
            break;
          case "knowledge":
            await this.loadKnowledgeBase();
            break;
          case "tools":
            await this.loadTools();
            break;
          case "advisors":
            await this.loadAdvisors();
            break;
          case "models":
            await this.loadModels();
            break;
          case "tasks":
            await this.loadTasks();
            break;
        }
      } catch (error) {
        this.$message.error("加载数据失败: " + error.message);
      }
    },

    // Client methods
    async loadClients() {
      const response = await axios.get("client");
      this.clients = response.data;
    },

    onAddClient() {
      axios.post(`client`)
          .then(response => {
            this.$message.success("新增成功");
            this.loadClients();
          }).catch(error => {
        console.error("Error creating client:", error);
        this.$message.error("创建失败");
      });
    },
    async editClient(client) {
      const response = await axios.get(`client/${client.id}`);
      const options = response.data.optionsJsonStr || "{}";
      this.editingClient = {
        ...client,
        options: options,
        mcpIds: response.data.mcpIds,
        advisorIds: response.data.advisorIds,
      };
      this.showClientConfig = true;
    },
    onClientConfigBack() {
      this.showClientConfig = false;
      this.editingClient = null;
      this.loadClients();
    },

    // Prompt methods
    async loadPrompts() {
      const response = await axios.get("prompt");
      this.prompts = response.data;
    },

    editPrompt(prompt) {
      this.promptForm = {...prompt};
      this.showPromptDialog = true;
    },

    async savePrompt() {
      try {
        if (this.promptForm.id) {
          await axios.put("prompt", this.promptForm);
        } else {
          const requestDTO = {
            promptName: this.promptForm.promptName,
            description: this.promptForm.description,
            promptContent: this.promptForm.promptContent
          };

          await axios.post("prompt", requestDTO);
        }
        this.$message.success("保存成功");
        this.showPromptDialog = false;
        this.resetPromptForm();
        await this.loadPrompts();
      } catch (error) {
        this.$message.error("保存失败: " + error.message);
      }
    },

    async deletePrompt(id) {
      try {
        await this.$confirm("确认删除此提示？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        });
        await axios.delete(`prompt?promptId=${id}`);
        this.$message.success("删除成功");
        await this.loadPrompts();
      } catch (error) {
        if (error !== "cancel") {
          this.$message.error("删除失败: " + error.message);
        }
      }
    },

    resetPromptForm() {
      this.promptForm = {
        id: null,
        promptName: "",
        description: "",
        promptContent: "",
      };
    },

    // Knowledge Base methods
    async loadKnowledgeBase() {
      const response = await axios.get("rag");
      this.knowledgeBase = response.data;
    },

    handleFileChange(file, fileList) {
      // fileList 可能为 undefined 或对象，需强制转为数组
      if (Array.isArray(fileList)) {
        this.fileList = fileList;
      } else if (
          fileList &&
          typeof fileList === "object" &&
          fileList.length !== undefined
      ) {
        // 兼容 Element UI 可能传递的类数组对象
        this.fileList = Array.prototype.slice.call(fileList);
      } else {
        // 兼容只传 file 的情况
        this.fileList = file ? [file] : [];
      }
    },

    async saveKnowledge() {
      try {
        const formData = new FormData();
        formData.append("ragName", this.knowledgeForm.ragName);
        formData.append("knowledgeTag", this.knowledgeForm.knowledgeTag);

        this.fileList.forEach((file) => {
          formData.append("files", file.raw);
        });

        await axios.post("rag", formData);
        this.$message.success("保存成功");
        this.showKnowledgeDialog = false;
        this.resetKnowledgeForm();
        await this.loadKnowledgeBase();
      } catch (error) {
        this.$message.error("保存失败: " + error.message);
      }
    },

    async analyzeGitRepo() {
      try {
        const formData = new FormData();
        formData.append("repoUrl", this.gitForm.repoUrl);
        formData.append("userName", this.gitForm.userName);
        formData.append("token", this.gitForm.token);

        await axios.post("rag/git", formData);
        this.$message.success("仓库解析成功");
        this.showGitDialog = false;
        this.resetGitForm();
        await this.loadKnowledgeBase();
      } catch (error) {
        this.$message.error("仓库解析失败: " + error.message);
      }
    },

    async deleteKnowledge(id) {
      try {
        await this.$confirm("确认删除此知识库？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        });
        await axios.delete(`rag/${id}`);
        this.$message.success("删除成功");
        await this.loadKnowledgeBase();
      } catch (error) {
        if (error !== "cancel") {
          this.$message.error("删除失败: " + error.message);
        }
      }
    },

    resetKnowledgeForm() {
      this.knowledgeForm = {
        ragName: "",
        knowledgeTag: "",
      };
      this.fileList = [];
    },

    resetGitForm() {
      this.gitForm = {
        repoUrl: "",
        userName: "",
        token: "",
      };
    },

    // Tools methods
    async loadTools() {
      const response = await axios.get("tools");
      this.tools = response.data;
    },

    editTool(tool) {
      this.toolForm = {...tool};
      this.showToolDialog = true;
    },

    async saveTool() {
      try {
        // console.log("Saving tool:", JSON.stringify(this.toolForm));
        if (this.toolForm.id) {
          await axios.put("tools", this.toolForm);
        } else {
          await axios.post("tools", this.toolForm);
        }
        this.$message.success("保存成功");
        this.showToolDialog = false;
        this.resetToolForm();
        await this.loadTools();
      } catch (error) {
        this.$message.error("保存失败: " + error.message);
      }
    },

    // 工具删除与状态变更
    async deleteTool(id) {
      try {
        await this.$confirm("确认删除此工具？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        });
        const res = await axios.delete("tools", {
          params: {toolId: id},
        });
        if (res.data === true) {
          this.$message.success("删除成功");
          await this.loadTools();
        }
      } catch (error) {
        if (error !== "cancel") {
          this.$message.error("该工具有关联数据，无法删除");
        }
      }
    },
    async changeToolStatus(row) {
      try {
        const res = await axios.put("tools/status", null, {
          params: {toolId: row.id},
        });
        if (res.data === true) {
          this.$message.success("状态变更成功");
          await this.loadTools();
        }
      } catch (error) {
        this.$message.error("该工具有关联数据，无法变更");
      }
    },
    async changeClientStatus(row) {

      const res = await axios.put(`client`, null, {
        params: {
          clientId: row.id,
          name: row.clientName,
          description: row.description,
          status: row.status === 1 ? 0 : 1,
        },
      });
      if (res.data) {
        this.$message.success("状态变更成功");
        await this.loadClients();
      }
    },

    // Advisor methods
    async loadAdvisors() {
      const response = await axios.get("advisor");
      this.advisors = response.data;
    },

    editAdvisor(advisor) {
      this.advisorForm = {...advisor};
      this.showAdvisorDialog = true;
    },

    async saveAdvisor() {
      try {
        const formData = new FormData();
        formData.append("extParams", this.advisorForm.extParam);

        await axios.put(
            `advisor?advisorId=${this.advisorForm.id}&name=${this.advisorForm.advisorName}&basic=${this.advisorForm.basic}`,
            formData
        );
        this.$message.success("保存成功");
        this.showAdvisorDialog = false;
        await this.loadAdvisors();
      } catch (error) {
        this.$message.error("保存失败: " + error.message);
      }
    },

    // 顾问删除与状态变更
    async deleteAdvisor(id) {
      try {
        await this.$confirm("确认删除此顾问？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        });
        const res = await axios.delete("advisor", {
          params: {advisorId: id},
        });
        if (res.data === true) {
          this.$message.success("删除成功");
          await this.loadAdvisors();
        }
      } catch (error) {
        if (error !== "cancel") {
          this.$message.error("该顾问有关联数据，无法删除");
        }
      }
    },
    async changeAdvisorStatus(row) {
      try {
        const res = await axios.put("advisor/status", null, {
          params: {advisorId: row.id},
        });
        if (res.data === true) {
          this.$message.success("状态变更成功");
          await this.loadAdvisors();
        }
      } catch (error) {
        this.$message.error("存在有关联数据，无法变更");
      }
    },

    // Model methods
    async loadModels() {
      const response = await axios.get("model");
      // 直接赋值为后端返回的数组
      this.models = response.data;
      // 默认展开所有分组
      this.modelCollapseActiveNames = [
        ...new Set(this.models.map((m) => m.modelName)),
      ];
    },

    editModel(model) {
      this.modelForm = {...model};
      this.showModelDialog = true;
    },

    async saveModel() {
      try {
        if (this.modelForm.id) {
          await axios.put("model", this.modelForm);
        } else {
          await axios.post("model", this.modelForm);
        }
        this.$message.success("保存成功");
        this.showModelDialog = false;
        this.resetModelForm();
        await this.loadModels();
      } catch (error) {
        this.$message.error("保存失败: " + error.message);
      }
    },

    // 模型删除与状态变更
    async deleteModel(id) {
      try {
        await this.$confirm("确认删除此模型？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        });
        const res = await axios.delete("model", {
          params: {modelId: id},
        });
        if (res.data === true) {
          this.$message.success("删除成功");
          await this.loadModels();
        } else {
          this.$message.error("删除失败：该模型有关联数据，无法删除");
        }
      } catch (error) {
        if (error !== "cancel") {
          this.$message.error("删除失败: " + error.message);
        }
      }
    },
    async changeModelStatus(row) {
      try {
        const res = await axios.put("model/status", null, {
          params: {modelId: row.id},
        });
        if (res.data === true) {
          this.$message.success("状态变更成功");
          await this.loadModels();
        } else {
          this.$message.error("状态变更失败：该模型有关联数据，无法变更");
        }
      } catch (error) {
        this.$message.error("状态变更失败: " + error.message);
      }
    },

    // 智能体任务相关方法
    async loadTasks() {
      const response = await axios.get("task");
      this.tasks = response.data;
    },
    onAddTask() {
      this.resetTaskForm();
      this.showTaskDialog = true;
    },
    editTask(task) {
      this.taskForm = {...task};
      this.showTaskDialog = true;
    },
    async saveTask() {
      try {
        if (this.taskForm.id) {
          await axios.put(`task`, this.taskForm);
        } else {
          this.taskForm.status = null;
          await axios.post("task", this.taskForm);
        }
        this.$message.success("保存成功");
        this.showTaskDialog = false;
        this.resetTaskForm();
        await this.loadTasks();
      } catch (error) {
        this.$message.error("保存失败: " + error.message);
      }
    },
    async deleteTask(id) {
      try {
        await this.$confirm("确认删除此任务？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        });
        const response = await axios.delete(`task/${id}`);
        if (response.data) {
          this.$message.success("删除成功");
          await this.loadTasks();
        } else this.$message.warning("请先停用任务");
      } catch (error) {
        if (error !== "cancel") {
          this.$message.error("删除失败: " + error.message);
        }
      }
    },
    async showTaskLog(taskId) {
      this.showTaskLogDialog = true;
      this.taskLogs = [];
      try {
        const res = await this.$axios
            ? this.$axios.get(`task/log/${taskId}`)
            : await import("axios").then(ax => ax.default.get(`task/log/${taskId}`));
        this.taskLogs = res.data;
      } catch (e) {
        this.$message.error("获取日志失败");
      }
    },
    resetTaskForm() {
      this.taskForm = {
        id: null,
        agentId: "",
        taskName: "",
        description: "",
        cronExpression: "",
        taskParam: "",
        status: 1,
      };
    },
    resetToolForm() {
      this.toolForm = {
        id: null,
        mcpName: "",
        transportType: "sse",
        requestTimeout: 180,
        transportConfig: `请输入JSON格式的配置参数，例如：
{
  "key1": "value1",
  "key2": "value2"
}`,
      };
    },
    resetModelForm() {
      this.modelForm = {
        id: null,
        modelName: "",
        modelType: "",
        modelVersion: "",
        baseUrl: "",
        apiKey: "",
        completionsPath: "/v1/chat/completions",
        timeout: 30,
        status: 1,
      };
    },
    goBackToChat() {
      this.$router.push("/ai-chat");
    },
  },
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
  "Helvetica Neue", Arial, sans-serif;
}

.sidebar-menu {
  border: none !important;
}

.sidebar-menu .el-menu-item {
  border-radius: 8px;
  margin: 4px 12px;
  transition: all 0.3s ease;
}

.sidebar-menu .el-menu-item:hover {
  background-color: #333 !important;
}

.sidebar-menu .el-menu-item.is-active {
  background-color: #00d4aa !important;
  color: white !important;
}

.el-table {
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
}

.el-button {
  border-radius: 6px;
  font-weight: 500;
}

.el-button--primary {
  background-color: #00d4aa;
  border-color: #00d4aa;
}

.el-button--primary:hover {
  background-color: #00c299;
  border-color: #00c299;
}

.el-dialog {
  border-radius: 12px;
}

.el-dialog__header {
  background-color: #f8f9fa;
  border-radius: 12px 12px 0 0;
  padding: 20px 24px;
}

.el-dialog__title {
  font-weight: 600;
  color: #1a1a1a;
}

.el-form-item__label {
  font-weight: 500;
  color: #333;
}

.el-input__inner,
.el-textarea__inner {
  border-radius: 6px;
  border: 1px solid #e1e5e9;
  transition: all 0.3s ease;
}

.el-input__inner:focus,
.el-textarea__inner:focus {
  border-color: #00d4aa;
  box-shadow: 0 0 0 2px rgba(0, 212, 170, 0.1);
}

.el-tag {
  border-radius: 4px;
  font-weight: 500;
}

.el-upload {
  width: 100%;
}

.el-upload-dragger {
  border-radius: 8px;
  border: 2px dashed #d9d9d9;
  transition: all 0.3s ease;
}

.el-upload-dragger:hover {
  border-color: #00d4aa;
}

.el-collapse-item__content {
  padding: 0 0 12px 0;
}
</style>