<template>
  <div class="config-container">
    <el-card class="config-card">
      <div slot="header" class="card-header">
        <span>AI 客户端配置</span>
        <el-button type="text" @click="goBack">返回</el-button>
      </div>

      <el-form
        ref="clientForm"
        :model="clientForm"
        label-width="100px"
        :rules="rules"
      >
        <!-- 基本信息 -->
        <el-form-item label="客户端名称" prop="clientName">
          <el-input
            v-model="clientForm.clientName"
            placeholder="请输入客户端名称"
          ></el-input>
        </el-form-item>

        <el-form-item label="描述" prop="description">
          <el-input
            type="textarea"
            v-model="clientForm.description"
            placeholder="请输入描述信息"
          ></el-input>
        </el-form-item>

        <!-- 模型配置 -->
        <el-form-item label="选择模型" prop="modelId">
          <el-select
            v-model="clientForm.modelId"
            placeholder="请选择模型"
            style="width: 100%"
          >
            <el-option-group
                v-for="(models, modelName) in availableModels"
                :key="modelName"
                :label="modelName"
            >
              <el-option
                  v-for="model in models"
                  :key="model.id"
                  :label="`${model.modelVersion}`"
                  :value="model.id"
              />
            </el-option-group>
          </el-select>
        </el-form-item>

        <!-- 系统提示词配置 -->
        <el-form-item label="系统提示词" prop="systemPromptId">
          <el-select
            v-model="clientForm.systemPromptId"
            placeholder="请选择系统提示词"
            style="width: 100%"
            @change="handlePromptChange"
          >
            <el-option
              v-for="prompt in systemPrompts"
              :key="prompt.id"
              :label="prompt.promptName"
              :value="prompt.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <!-- Advisor配置 -->
        <el-divider content-position="left"></el-divider>
        <el-form-item label="选择Advisor">
          <el-transfer
            v-model="clientForm.advisorIds"
            :data="allAdvisors"
            :titles="['可用Advisor', '已选Advisor']"
            :props="{
              key: 'id',
              label: 'advisorName',
            }"
          ></el-transfer>
        </el-form-item>

        <!-- MCP工具配置 -->
        <el-divider content-position="left"></el-divider>
        <el-form-item label="选择MCP工具">
          <el-transfer
            v-model="clientForm.mcpIds"
            :data="allMcpTools"
            :titles="['可用MCP工具', '已选MCP工具']"
            :props="{
              key: 'id',
              label: 'mcpName',
            }"
          ></el-transfer>
        </el-form-item>
        <el-form-item label="选项配置" prop="options">
          <el-input
            type="textarea"
            v-model="clientForm.options"
            rows="8"
          ></el-input>
        </el-form-item>

        <!-- 操作按钮 -->
        <el-form-item>
          <el-button type="primary" @click="saveConfig" :loading="isSaving"
            >保存配置</el-button
          >
          <el-button @click="resetForm">重置</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import axios from "axios";
import { clientMixin } from "@/mixins/clientMixin";

export default {
  mixins: [clientMixin],
  name: "ClientConfig",
  props: {
    clientId: {
      type: [Number, String],
      default: null,
    },
    clientData: {
      type: Object,
      default: null,
    },
  },
  data() {
    return {
      baseUrl: "http://localhost:8091",
      clientForm: {
        id: null,
        clientName: "",
        description: "",
        modelId: null,
        systemPromptId: null,
        advisorIds: [],
        mcpIds: [],
        options: "",
      },
      availableModels: {},
      systemPrompts: [],
      selectedPromptContent: "",
      allAdvisors: [],
      allMcpTools: [],
      isSaving: false,
      rules: {
        clientName: [
          { required: true, message: "请输入客户端名称", trigger: "blur" },
          {
            min: 2,
            max: 50,
            message: "长度在 2 到 50 个字符",
            trigger: "blur",
          },
        ],
        modelId: [{ required: true, message: "请选择模型", trigger: "change" }],
        systemPromptId: [
          { required: true, message: "请选择系统提示词", trigger: "change" },
        ],
      },
    };
  },
  created() {
    this.fetchModels();
    this.fetchSystemPrompts();
    this.fetchAdvisors();
    this.fetchMcpTools();

    if (this.clientData) {
      this.initClientForm(this.clientData);
    } else if (this.clientId) {
      this.fetchClientDetails();
    }
  },
  methods: {
    fetchModels() {
      axios
          .get(`${this.baseUrl}/api/v1/model`)
          .then((response) => {
            // 假设 response.data 是一个模型列表
            const models = response.data;

            // 将模型按名称分组
            const groupedModels = {};
            models.forEach(model => {
              if (!groupedModels[model.modelName]) {
                groupedModels[model.modelName] = [];
              }
              groupedModels[model.modelName].push(model);
            });

            this.availableModels = groupedModels;
          })
          .catch((error) => {
            console.error("Error fetching models:", error);
            this.$message.error("获取模型列表失败");
          });
    },
    fetchSystemPrompts() {
      axios
        .get(`${this.baseUrl}/api/v1/prompt`)
        .then((response) => {
          this.systemPrompts = response.data;
        })
        .catch((error) => {
          console.error("Error fetching system prompts:", error);
          this.$message.error("获取系统提示词失败");
        });
    },
    fetchAdvisors() {
      axios
        .get(`${this.baseUrl}/api/v1/advisor`)
        .then((response) => {
          this.allAdvisors = response.data.map((advisor) => ({
            id: advisor.id,
            advisorName: advisor.advisorName,
            advisorType: advisor.advisorType,
            disabled: advisor.status !== 1,
          }));
        })
        .catch((error) => {
          console.error("Error fetching advisors:", error);
          this.$message.error("获取Advisor列表失败");
        });
    },
    fetchMcpTools() {
      axios
        .get(`${this.baseUrl}/api/v1/tools`)
        .then((response) => {
          this.allMcpTools = response.data.map((tool) => ({
            id: tool.id,
            mcpName: tool.mcpName,
            transportType: tool.transportType,
            disabled: tool.status !== 1,
          }));
        })
        .catch((error) => {
          console.error("Error fetching MCP tools:", error);
          this.$message.error("获取MCP工具列表失败");
        });
    },
    fetchClientDetails() {
      axios
        .get(`${this.baseUrl}/api/v1/client/${this.clientId}`)
        .then((response) => {
          this.initClientForm(response.data);
        })
        .catch((error) => {
          console.error("Error fetching client details:", error);
          this.$message.error("获取客户端详情失败");
        });
    },
    initClientForm(clientData) {
      this.clientForm = {
        id: clientData.id,
        clientName: clientData.clientName,
        description: clientData.description || "",
        modelId: clientData.modelId,
        systemPromptId: clientData.systemPromptId,
        advisorIds: (clientData.advisorIds || []).map(Number),
        mcpIds: (clientData.mcpIds || []).map(Number),
        options: clientData.options || "",
      };
      if (clientData.systemPromptId) {
        this.updatePromptContent(clientData.systemPromptId);
      }
    },
    handlePromptChange(promptId) {
      this.updatePromptContent(promptId);
    },
    updatePromptContent(promptId) {
      const selectedPrompt = this.systemPrompts.find((p) => p.id === promptId);
      this.selectedPromptContent = selectedPrompt
        ? selectedPrompt.promptContent
        : "";
    },
    saveConfig() {
      this.$refs.clientForm.validate((valid) => {
        if (!valid) {
          return false;
        }
        this.isSaving = true;
        const saveMethod = this.clientForm.id
          ? this.updateClientAndConfig
          : this.createClientAndConfig;
        saveMethod()
          .then(() => {
            this.$message.success("配置保存成功");
            this.goBack();
          })
          .catch((error) => {
            console.error("Error saving config:", error);
            this.$message.error("保存配置失败");
          })
          .finally(() => {
            this.isSaving = false;
          });
      });
    },
    async updateClientAndConfig() {
      // 1. 更新基本信息
      await axios.put(`${this.baseUrl}/api/v1/client`, null, {
        params: {
          clientId: this.clientForm.id,
          name: this.clientForm.clientName,
          description: this.clientForm.description,
        },
      });
      // 2. 更新配置
      await this.updateClientConfig(this.clientForm.id);
    },
    async createClientAndConfig() {
      // 1. 创建客户端
      const res = await axios.post(`${this.baseUrl}/api/v1/client`);
      // 2. 获取新id（需后端返回新id，若无则需额外接口获取）
      let newId = res.data && res.data.id;
      if (!newId) {
        // fallback: 重新拉取所有客户端，取最大id
        const listRes = await axios.get(`${this.baseUrl}/api/v1/client`);
        const arr = Array.isArray(listRes.data) ? listRes.data : [];
        newId = arr.length ? Math.max(...arr.map((c) => c.id)) : 1;
      }
      await this.updateClientConfig(newId);
    },
    async updateClientConfig(clientId) {
      let optionsObj = {};
      try {
        optionsObj = this.clientForm.options
          ? JSON.parse(this.clientForm.options)
          : {};
      } catch (e) {
        this.$message.error("Options配置项必须为合法JSON");
        throw e;
      }
      await axios.put(`${this.baseUrl}/api/v1/client/config`, {
        clientId: clientId,
        modelId: this.clientForm.modelId,
        systemPromptId: this.clientForm.systemPromptId,
        advisorIds: this.clientForm.advisorIds.map(Number),
        mcpIds: this.clientForm.mcpIds.map(Number),
        options: optionsObj,
      });
    },
    resetForm() {
      if (this.clientId) {
        this.fetchClientDetails();
      } else if (this.clientData) {
        this.initClientForm(this.clientData);
      } else {
        this.$refs.clientForm.resetFields();
        this.selectedPromptContent = "";
      }
    },
    goBack() {
      this.$emit("back");
    },
  },
};
</script>

<style scoped>
.config-container {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.config-card {
  margin-bottom: 20px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.el-divider {
  margin: 20px 0;
}

.el-transfer {
  margin-bottom: 15px;
}
</style>